.main-header {
  text-align: center;
  color: white;
  background: #3e3343;
}

.main-header .hero {
  overflow: hidden;
  min-height: 100vh;
  background-image: url(/img/hero-4.jpeg);
  background-attachment: fixed;
  background-size: cover;
  background-position: center center;
  position: relative;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
  object-fit: cover;
}

.main-header .hero .container-head {
  margin-right: auto;
  margin-left: auto;
  padding-left: 15px;
  padding-right: 15px;
  min-width: 300px;
  max-width: 1200px;
}

.main-header .hero .container-head .head-top {
  text-align: center;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  color: white;
}

.main-header .hero .container-head .head-top .marca {
  display: inline-block;
  font-family: "slabo", sans-serif;
}

.main-header .hero .container-head .head-top .marca .slogan {
  font-size: 20px;
  text-transform: uppercase;
}

.main-header .hero .container-head .head-top .marca .name {
  overflow: hidden;
  text-transform: none;
  font-size: 120px;
  line-height: 1;
  padding: 0px;
}

.main-header .hero .container-head .head-top .marca .name a {
  color: white;
  text-decoration: none;
}

.main-header .hero .container-head .head-top .marca .name a p {
  margin: 0;
}

.main-header .hero .container-head .head-top .fecha {
  font-size: 14px;
  line-height: 32px;
  position: relative;
}

.main-header .hero .container-head .head-top .fecha::before {
  position: absolute;
  /* display: inline-block; */
  content: '';
  width: 97px;
  height: 1px;
  left: 50%;
  top: 50%;
  background: #fff;
  transform: translateX(-50%) translateY(-50%) rotate(45deg);
}

.main-header .hero .container-head .head-top .fecha::after {
  position: absolute;
  content: '';
  width: 97px;
  height: 1px;
  left: 50%;
  top: 50%;
  background: #fff;
  transform: translateX(-50%) translateY(-50%) rotate(-45deg);
}

.main-header .hero .container-head .head-top span + span {
  font-size: 16px;
  margin-left: 44px;
}

.main-header .hero .container-head .flecha {
  font-size: 14px;
  line-height: 32px;
  position: relative;
  padding: 20px;
  width: 100px;
  margin: 50px auto;
  transition: all .25s;
  line-height: 66px;
  cursor: pointer;
}

.main-header .hero .container-head .flecha::before {
  position: absolute;
  content: '';
  width: 50px;
  height: 2px;
  left: 50%;
  top: 83%;
  background: #fff;
  transform: translateX(-83%) translateY(-83%) rotate(45deg);
  cursor: pointer;
}

.main-header .hero .container-head .flecha::after {
  cursor: pointer;
  position: absolute;
  content: '';
  width: 50px;
  height: 2px;
  left: 85%;
  top: 83%;
  background: #fff;
  transform: translateX(-83%) translateY(-83%) rotate(-45deg);
}

