  margin: 0 auto;
  border: 2px solid white;
  padding: 20px;
  font-family: "monserrat", sans-serif;
}

.foot-about .store .two .box img {
  width: 100%;
}

.foot-about .store .two .box h6 {
  font-size: 18px;
  line-height: 1.66;
  color: #9d98a0;
  margin: 20px;
}

.foot-about .store .two .box a {
  font-size: 30px;
  line-height: 1;
  color: #3e3343;
  font-weight: bold;
  cursor: pointer;
}

.foot-about .contact {
  width: 80%;
  margin: 0 auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 100px 0 0 0;
}

.foot-about .contact .redes {
  color: white;
}

.foot-about .contact .redes p {
  text-align: center;
  font-family: "monserrat", sans-serif;
}

.foot-about .contact .redes ul {
  list-style: none;
  display: flex;
  flex-flow: wrap;
  justify-content: center;
  padding: 0px;
}

.foot-about .contact .redes ul li {
  margin: 0 10px;
}

.foot-about .contact .redes ul li a {
  color: white;
  font-size: 36px;
  text-decoration: none;
  width: 200px;
  height: 200px;
  border: 1px solid white;
  padding: 10px;
  transition: 1s all ease;
}

.foot-about .contact .redes ul li a:hover {
  opacity: .5;
  transition: 1s all ease;
}

.button {
  position: relative;
  margin-top: 27px;
  display: inline-block;
  text-align: center;
  line-height: 1.2;
  max-width: 100%;
  border-style: solid;
  font-weight: 500;
  overflow: hidden;
  cursor: pointer;
  transition: .3s all ease;
  padding: 24px 24px;
  font-size: 17px;
  padding-right: 93px;
}

.button.primary {
  color: #787a7c;
  background: #fff;
  border-color: #eeeeee;
}

.button.primary::before {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 58px;
  content: '';
  border-left: 2px solid #3f3545;
  transition: .4s all ease;
  font-size: 24.14px;
  padding-right: 10px;
  line-height: 0.70423em;
  border-color: #eeeeee;
}

.button.primary::after {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 10px;
  font-size: 35px;
  line-height: 0.70423em;
  content: '\ea42';
  padding-right: 10px;
  padding-left: 10px;
  margin-left: 10px;
  line-height: 72px;
  color: white;
}

.button.primary:hover {
  background: #3f3545;
  color: white;
}
